
 html {
    font-size: 62.5%;
    line-height: 1.15; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
  }

  body {
    font: 1.6rem 'Raleway', sans-serif;
    margin: 0;
    background: var(--colorMain);
    font-weight: 400;
    text-rendering: optimizeLegibility;
  }

  * {
    box-sizing: border-box;
  }
  
  main {
    display: block;
  }

  h1 {
    font-size: 2em;
    margin: 0;
  }

  hr {
    box-sizing: content-box; /* 1 */
    height: 0; /* 1 */
    overflow: visible; /* 2 */
  }
  
  pre {
    font-family: monospace, monospace; /* 1 */
    font-size: 1em; /* 2 */
  }

  a {
    background-color: transparent;
  }
  
  abbr[title] {
    border-bottom: none; /* 1 */
    text-decoration: underline; /* 2 */
    text-decoration: underline dotted; /* 2 */
  }
 
  b,
  strong {
    font-weight: bolder;
  }
  
  /**
   * 1. Correct the inheritance and scaling of font size in all browsers.
   * 2. Correct the odd `em` font sizing in all browsers.
   */
  
  code,
  kbd,
  samp {
    font-family: monospace, monospace; /* 1 */
    font-size: 1em; /* 2 */
  }
  
  /**
   * Add the correct font size in all browsers.
   */
  
  small {
    font-size: 80%;
  }
  
  /**
   * Prevent `sub` and `sup` elements from affecting the line height in
   * all browsers.
   */
  
  p, ul, li {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  a {
    text-decoration: none;
    color: inherit;
  }

  sub,
  sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
  }
  
  sub {
    bottom: -0.25em;
  }
  
  sup {
    top: -0.5em;
  }
  
  /* Embedded content
     ========================================================================== */
  
  /**
   * Remove the border on images inside links in IE 10.
   */
  
  img {
    border-style: none;
  }
  
  /* Forms
     ========================================================================== */
  
  /**
   * 1. Change the font styles in all browsers.
   * 2. Remove the margin in Firefox and Safari.
   */
  
  button,
  input,
  optgroup,
  select,
  textarea {
    font-family: inherit; /* 1 */
    font-size: 100%; /* 1 */
    line-height: 1.15; /* 1 */
    margin: 0; /* 2 */
  }
  
  /**
   * Show the overflow in IE.
   * 1. Show the overflow in Edge.
   */
  
  button,
  input { /* 1 */
    overflow: visible;
  }
  
  /**
   * Remove the inheritance of text transform in Edge, Firefox, and IE.
   * 1. Remove the inheritance of text transform in Firefox.
   */
  
  button,
  select { /* 1 */
    text-transform: none;
  }
  
  /**
   * Correct the inability to style clickable types in iOS and Safari.
   */
  
  button,
  [type="button"],
  [type="reset"],
  [type="submit"] {
    -webkit-appearance: button;
  }
  
  /**
   * Remove the inner border and padding in Firefox.
   */
  
  button::-moz-focus-inner,
  [type="button"]::-moz-focus-inner,
  [type="reset"]::-moz-focus-inner,
  [type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
  }
  
  /**
   * Restore the focus styles unset by the previous rule.
   */
  
  button:-moz-focusring,
  [type="button"]:-moz-focusring,
  [type="reset"]:-moz-focusring,
  [type="submit"]:-moz-focusring {
    outline: 1px dotted ButtonText;
  }
  
  /**
   * Correct the padding in Firefox.
   */
  
  fieldset {
    padding: 0.35em 0.75em 0.625em;
  }
  
  /**
   * 1. Correct the text wrapping in Edge and IE.
   * 2. Correct the color inheritance from `fieldset` elements in IE.
   * 3. Remove the padding so developers are not caught out when they zero out
   *    `fieldset` elements in all browsers.
   */
  
  legend {
    box-sizing: border-box; /* 1 */
    color: inherit; /* 2 */
    display: table; /* 1 */
    max-width: 100%; /* 1 */
    padding: 0; /* 3 */
    white-space: normal; /* 1 */
  }
  
  /**
   * Add the correct vertical alignment in Chrome, Firefox, and Opera.
   */
  
  progress {
    vertical-align: baseline;
  }
  
  /**
   * Remove the default vertical scrollbar in IE 10+.
   */
  
  textarea {
    overflow: auto;
  }
  
  /**
   * 1. Add the correct box sizing in IE 10.
   * 2. Remove the padding in IE 10.
   */
  
  [type="checkbox"],
  [type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
  }
  
  /**
   * Correct the cursor style of increment and decrement buttons in Chrome.
   */
  
  [type="number"]::-webkit-inner-spin-button,
  [type="number"]::-webkit-outer-spin-button {
    height: auto;
  }
  
  /**
   * 1. Correct the odd appearance in Chrome and Safari.
   * 2. Correct the outline style in Safari.
   */
  
  [type="search"] {
    -webkit-appearance: textfield; /* 1 */
    outline-offset: -2px; /* 2 */
  }
  
  /**
   * Remove the inner padding in Chrome and Safari on macOS.
   */
  
  [type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
  }
  
  /**
   * 1. Correct the inability to style clickable types in iOS and Safari.
   * 2. Change font properties to `inherit` in Safari.
   */
  
  ::-webkit-file-upload-button {
    -webkit-appearance: button; /* 1 */
    font: inherit; /* 2 */
  }
  
  /* Interactive
     ========================================================================== */
  
  /*
   * Add the correct display in Edge, IE 10+, and Firefox.
   */
  
  details {
    display: block;
  }
  
  /*
   * Add the correct display in all browsers.
   */
  
  summary {
    display: list-item;
  }
  
  /* Misc
     ========================================================================== */
  
  /**
   * Add the correct display in IE 10+.
   */
  
  template {
    display: none;
  }
  
  /**
   * Add the correct display in IE 10.
   */
  
  [hidden] {
    display: none;
  }
  
  .br {
    display: block;
    @media(--mobile-screen) {
      display: block;
    }
  }

  .container {
      display: block;
      max-width: 110rem;
      margin: 0 auto;
      padding: 0 1.7rem;
  }

  .section {

    &__inner {
      padding: 10rem 0;
    }

    &__heading {
      font-weight: 600;
      font-size: 3.6rem;
      line-height: 120%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #1D1D1D;
      margin-bottom: 2.4rem;

      @media(--mobile-screen) {
       
      }
      
    }

    &__text {
      font-weight: 500;
      font-size: 1.2rem;
      line-height: 150%;
      letter-spacing: 0.02em;
      color: #000000;
      margin-bottom: 1.5rem;
    }

    &__title {
      font-weight: 600;
      font-size: 1.2rem;
      line-height: 1.4rem;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #818181;
      margin-bottom: 1rem;
    }

    &__wrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    &__side {
      width: 40%;

      &:last-child {
        width: 49%;
        @media(--mobile-screen) {
          width: 100%;
        }
      }
    }

    &__controls {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin-top: 6rem;
    }

    &__button {
      display: block;
      width: 48%;
      text-align: center;
    }

  }

  .app__notice {
    font-size: 2rem;
    line-height: 170%;
    letter-spacing: 0.15px;
    color: #333333;
    padding: 3.5rem 13.3rem 3.5rem 3.5rem;
    margin-bottom: 2rem;
    @media(--mobile-screen) {
      padding: 2.5rem;
    }
  }

  .app__notice.green {
    background-color: #E9FCEE;
    border-left: 5px solid #2BB151;
  }

  .app__notice.yellow {
    background-color: #FBFCE9;
    border-left: 5px solid #F5D83E;
  }

  .date_img {

    &:after {
      content: '';
      top: 0rem;
      right: 8.5rem;
      position: absolute;
      display: block;
      width: .2rem;
      height: 6.4rem;
      background: #EBEBEB;
      z-index: 0;
      // #EBEBEB
    }

  }

  input {
    @media(--mobile-screen) {
      font-size: 1.5rem;
    }
  }

  input[type="date"]::-webkit-inner-spin-button {
    // display: none;
  }
  
  input[type="date"]::-webkit-calendar-picker-indicator{
    //background: url('../img/icons/calendar.svg') no-repeat;
    //padding: 0;
    cursor: pointer;
  }

  .call-nav {

    &__inner {
      padding-top: 3.3rem;
    }

    &__items {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    &__item {
      // max-width: 17.7rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-weight: 600;
      font-size: 1.6rem;
      line-height: 120%;
      background: var(--colorWhite);
      letter-spacing: 0.02em;
      color: #1D1D1D;
      box-shadow: var(--shadow);
      overflow: hidden;
      @media(--mobile-screen) {
       width: 100%;
       margin-bottom: 2rem;
    }

      &:after {
        content: url('../img/icons/arrow.svg');
        display: block;
        background: var(--colorMain);
        width: 1rem;
        height: 2rem;
        background-size: contain;
        padding: 4.8rem 2rem;
        margin-left: 4rem;
      }
    }

    &__item.design {

      &:before {
        content: url('../img/icons/desproj.svg');
        display: block;
        background: #2C2C2C;
        width: 6.5rem;
        height: 6.5rem;
        background-size: contain;
        padding: 2.5rem;
        margin-right: 2rem;
      }
    }

    &__item.designer {

      &:before {
        content: url('../img/icons/des.svg');
        display: block;
        background: #2C2C2C;
        width: 6.5rem;
        height: 6.5rem;
        background-size: contain;
        padding: 2.5rem;
        margin-right: 2rem;
      }
    }

    &__item.measurer {

      &:before {
        content: url('../img/icons/measurer.svg');
        display: block;
        background: #2C2C2C;
        width: 6.5rem;
        height: 6.5rem;
        background-size: contain;
        padding: 2.5rem;
        margin-right: 2rem;
      }
    }
  }